	let instances;
	let preduction;
	let projection;
	let selectedNumber = null;
	const numClasses = 10;
	const numBins = 10;
	let binsByClasses = [];
	let Projection = [];
	let bins= [];
	let xExtent;
	let yExtent;
	let xScale;
	let yScale;
	let selectedclassId = null;
	let selectedTclassId = null;
	let selectedPclassId = null;
	let selectedId = null;
	let selectedTclasstype = null;
	let selectedPclasstype = null;
	let confidence =null;
	let TrueLable = false;
	let PredectLable = true;
	let TotalTrueLable = null;
	let TotalPredectLable = null;
	//console.log("confidience", confidence)
	
	onMount(async () => {
		const fetched = await fetch("static/prediction_results.json");
		instances = (await fetched.json()).test_instances;
		//console.log(instances);

		// Transform data

		for (let k = 0; k < numClasses; ++k) {
			let binsForClass = [];
			for (let b = 0; b <= numBins; ++b) {
				binsForClass.push({"class": k, "binNo": b, "instances":[instances.filter((record) => {return record.true_label == k && Math.floor(record.predicted_scores[record.true_label]*10.0) == b})] });
				
				//console.log(binsForClass);
			}
			
			binsByClasses.push({"class": k, "bins": binsForClass});
			bins.push({"class": k, "bins": binsForClass});
		}
		//console.log(binsByClasses)
		//console.log(binsByClasses[0].bins[0].instances[0][0].filename);

		instances.forEach(instance => {
		
				Projection.push({"id": instance.id,"Tclass": instance.true_label, "Pclass": instance.predicted_label,"projection": instance.projection,"scores": instance.predicted_scores,"image": instance.filename});
					
				
		});


		xExtent = extent(Projection, (d) => d.projection[0]);
		yExtent = extent(Projection, (d) => d.projection[1]);
		// console.log(yExtent)
		xScale = scaleLinear().domain(xExtent).range([buffer, width-buffer]);
		yScale = scaleLinear().domain(yExtent).range([buffer, height-buffer]);
		//console.log(xScale)
		Tclasses = new Set(Projection.map((d) => d.Tclass));
		//console.log(Tclasses)
		TcolorScale = scaleOrdinal().domain(Array.from(Tclasses)).range(schemeCategory10);
		//console.log(TcolorScale)
		PcolorScale = scaleOrdinal().domain(Array.from(Tclasses)).range(schemeCategory10);
		//console.log(PcolorScale)


		Projection.forEach(record => {
            record.selected = false;
        });
		binsByClasses.forEach(record => {
            record.selected = false;
        });

		// Trying to fix the nubers before droing the line
		// Projection.forEach(record => {
        //     for( let i =0; i< 10; i++){
		// 		record.scores[i] = (record.scores[i] *1000  <= 0.5) ?  scores[i] =0.0 : record.scores[i] =record.scores[i]  ;
		// 	}
			
        // });

		//  Fixing of Bins 4
		for(let i = 0; i < binsByClasses[4].bins[10].instances[0].length; i++) {
			binsByClasses[4].bins[9].instances[0].push(binsByClasses[4].bins[10].instances[0][i]);
			binsByClasses[4].bins[10].instances[0].splice(i, 1);
			i--; //decrement i IF we remove an item

		}


		let groupBy = (xs, key) => {
		return xs.reduce((rv, x) => {
			(rv[x[key]] = rv[x[key]] || []).push(x);
			return rv;
		}, {});
		};
		const by_groups = groupBy(Projection, "Tclass");
		//const by_bins = groupBy(Projection, Math.floor("scores"["Tclass"]*10) == "Tclass");
		//console.table("by_groups", by_groups);

		bin = Projection.filter((record) => {
			return Math.floor(record.scores[record.Tclass]*10) == record.Tclass
		});


	let mod = function (n, m) {
		var remain = n % m;
		return Math.floor(remain >= 0 ? remain : remain + m);
		};


	});

</script>

